@import "variables.scss";
@import "mixins.scss";

.work {
  &__list {
    margin-top: 40px;

    h1 {
      font-size: 1.4rem;
      margin-bottom: -5px;
    }

    .selected-work {
      display: grid !important;

      &__content {
        padding-top: 0;
      }
    }
  }

  &-page {
    animation: fade-up 500ms;
    @include flex-column-start;
    gap: 20px;
    margin: 30px 0 0 0;
    &__description {
      h1 {
        font-size: 1.3rem;
        line-height: 1;
        margin: 0 0 10px 0;
      }

      p {
        font-size: 1.1rem;
        line-height: 1.4;
      }
    }

    &__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 8px 15px;
      margin-bottom: 10px;

      > span {
        color: var(--body-color);
        gap: 7px;
        @include flex-center-base;

        > span {
          color: #fff;
          background-color: $hot-pink;
          line-height: 1;
          padding: 4px;
          border-radius: 4px;
          @include flex-center-base;
          display: inline-flex;
        }
      }
    }

    &__gallery {
      @include flex-column-start;
      gap: 30px 0;

      img {
        // max-width: 100%;
        border-radius: 10px;
        margin: -5px;
      }
    }
  }
}

@media (min-width: $sm) {
  .work {
    &-page {
      &__info {
        flex-direction: row;
      }
      &__gallery {
        gap: 20px 0;
        img {
          max-width: 100%;
          margin: 0;
        }
      }
    }
  }
}
